<template>
  <app-page>
    <el-tabs v-model="activeName" @tab-click="handleClick">
      <el-tab-pane label="我的" name="mine"></el-tab-pane>
      <el-tab-pane label="全部" name="all"></el-tab-pane>
      <el-tab-pane label="已發表" name="publish"></el-tab-pane>
      <el-tab-pane label="已排程" name="schedule"></el-tab-pane>
      <el-tab-pane label="垃圾桶" name="delete"></el-tab-pane>
      <el-tab-pane label="草稿區" name="draft"></el-tab-pane>
      <el-tab-pane label="審核中" name="check"></el-tab-pane>
    </el-tabs>

    <el-card>
      <!-- <div slot="header" class="card-header"> -->
        <el-row :gutter="10" style="margin-bottom: 1rem">
          <el-col :sm="12" >
            <el-date-picker
              class="field"
              v-model="d1"
              type="datetimerange"
              align="right"
              start-placeholder="Start Date"
              end-placeholder="End Date"
              default-value="2010-10-01">
            </el-date-picker>

            <el-date-picker
              class="field"
              v-model="d2"
              type="datetimerange"
              align="right"
              start-placeholder="Start Date"
              end-placeholder="End Date"
              default-value="2010-10-01">
            </el-date-picker>
          </el-col>

          <el-col :sm="8">
            <el-input class="field" placeholder="關鍵字"></el-input>
            <el-select class="field" v-model="category" placeholder="類別">
              <el-option label="電玩" value="電玩">電玩</el-option>
              <el-option label="政治" value="政治">政治</el-option>
            </el-select>
          </el-col>

          <el-col :sm="4"><el-button type="primary" style="width: 100%">搜尋</el-button></el-col>
        </el-row>
      <!-- </div> -->

      <el-table
        :fit="false"
        header-cell-class-name="cell-th"
        :data="tableData">
        <el-table-column prop="statusId" label="狀態" width="70"></el-table-column>
        <el-table-column prop="title" label="標題" width="200"></el-table-column>
        <el-table-column prop="accountName" label="作者" width="100"></el-table-column>
        <el-table-column prop="category" label="分類" width="70"></el-table-column>
        <el-table-column prop="tag" label="標籤" width="200"></el-table-column>
        <el-table-column prop="createTime" label="排程日期" width="140"></el-table-column>
        <el-table-column prop="updateTime" label="更新日期" width="140"></el-table-column>
        <el-table-column prop="seoTitle" label="SEO標籤" width="200"></el-table-column>
        <el-table-column prop="seoKeyWord" label="SEO關鍵字" width="200"></el-table-column>
        <el-table-column prop="traceContent" label="追蹤碼" width="100"></el-table-column>
        <el-table-column prop="pv" label="PV" width="70"></el-table-column>

        <el-table-column fixed="right" label="操作" width="110">
          <template slot-scope="scope">
            <el-dropdown trigger="click">
              <button class="svg-btn svg-btn__vert" @click="onActionClick(scope)"></button>
              <el-dropdown-menu slot="dropdown">
                <el-dropdown-item>查看</el-dropdown-item>
                <el-dropdown-item>修改</el-dropdown-item>
                <el-dropdown-item>刪除</el-dropdown-item>
              </el-dropdown-menu>
            </el-dropdown>
          </template>
        </el-table-column>
      </el-table>

      <div>
        <el-pagination
          class="pagination"
          :current-page.sync="currentPage"
          :page-sizes="[100, 200, 300, 400]"
          :page-size="100"
          layout="total, sizes, prev, pager, next, jumper"
          :total="400">
        </el-pagination>
      </div>
    </el-card>

  </app-page>
</template>

<script>
import articles from '@/assets/articles.json';

export default {
  data() {
    return {
      activeName: 'mine',
      d1: null,
      d2: null,
      category: null,
      tableData: articles,
      currentPage: 1
    };
  },
  methods: {
    handleClick(tab, event) {
      console.log(tab, event);
    },

    onActionClick(e) {
      console.log(e);
    }
  }
};
</script>

<style lang="scss" scoped>
.el-tabs {
  margin-bottom: 1rem !important;
}

.card-header {
}

.field {
  margin-bottom: 0.5rem;
  width: 100%
}

.pagination {
  margin: 1rem 0 1rem auto;
  float: right;
}
</style>