import Vue from 'vue';
import VueRouter from 'vue-router';
import Signin from '@/pages/Signin/Signin';
import Main from '@/pages/Main/Main';
import Articles from '@/pages/Articles';
import CreateArticle from '@/pages/CreateArticle';
import ArticleCategory from '@/pages/ArticleCategory';
import ArticleFavorite from '@/pages/ArticleFavorite';
import ArticleTag from '@/pages/ArticleTag';
import CreateTrackingCode from '@/pages/CreateTrackingCode';
import InsertTrackingCode from '@/pages/InsertTrackingCode';
import PageViews from '@/pages/PageViews';
import Reporter from '@/pages/Reporter';
import Resources from '@/pages/Resources';
import UploadResource from '@/pages/UploadResource';
import Accounts from '@/pages/Accounts';
import CreateAccount from '@/pages/CreateAccount';
import Authority from '@/pages/Authority';
import Roles from '@/pages/Roles';
import Pages from '@/pages/Pages';
import LockPage from '@/pages/LockPage';
import LockChannel from '@/pages/LockChannel';

Vue.use(VueRouter);

const routes = [
  // {path: '/', redirect: {name: 'Articles'}, name: 'Main', component: Main, children: [
  {path: '/', name: 'Main', component: Main, children: [
    {path: 'articles', component: Articles, name: 'Articles', meta: {text: '全部文章'}},
    {path: 'create-article', component: CreateArticle, name: 'CreateArticle', meta: {text: '新增文章'}},
    {path: 'article-category', component: ArticleCategory, name: 'ArticleCategory', meta: {text: '分類'}},
    {path: 'article-favorite', component: ArticleFavorite, name: 'ArticleFavorite', meta: {text: '我的最愛'}},
    {path: 'article-tag', component: ArticleTag, name: 'ArticleTag', meta: {text: '標籤'}},
    {path: 'create-tracking-code', component: CreateTrackingCode, name: 'CreateTrackingCode', meta: {text: '新增追蹤碼'}},
    {path: 'insert-tracking-code', component: InsertTrackingCode, name: 'InsertTrackingCode', meta: {text: '插入追蹤碼'}},
    {path: 'page-views', component: PageViews, name: 'PageViews', meta: {text: 'PV'}},
    {path: 'reporter', component: Reporter, name: 'Reporter', meta: {text: '快速找記者'}},
    {path: 'resources', component: Resources, name: 'Resources', meta: {text: '媒體庫'}},
    {path: 'upload-resource', component: UploadResource, name: 'UploadResource', meta: {text: '新增檔案'}},
    {path: 'accounts', component: Accounts, name: 'Accounts', meta: {text: '全部帳號'}},
    {path: 'create-account', component: CreateAccount, name: 'CreateAccount', meta: {text: '新增帳號'}},
    {path: 'authority', component: Authority, name: 'Authority', meta: {text: '權限管理'}},
    {path: 'roles', component: Roles, name: 'Roles', meta: {text: '角色管理'}},
    {path: 'pages', component: Pages, name: 'Pages', meta: {text: '全部頁面'}},
    {path: 'lock-page', component: LockPage, name: 'LockPage', meta: {text: '鎖定首頁'}},
    {path: 'lock-channel', component: LockChannel, name: 'LockChannel', meta: {text: '鎖定頻道頁'}}
  ]},
  {path: '/signin', component: Signin, name: 'Signin', meta: {auth: false}}
];


const router = new VueRouter({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes
});

// router.afterEach((to, from) => {});
router.beforeEach((to, from, next)=>{
  if (to.meta.auth !== false) {
    const token = sessionStorage.getItem('token');
    if (!token) {
      // logic for role permision
      // if (to.meta.role && to.meta.role !== store.state.user.role) {
      //   router.push(from.path);
      // } else {
      //   next();
      // }
      console.log('[router] next - has token');
      next();
    } else {
      router.push({name: 'Signin'});
    }
  } else {
    next();
  }
});

export default router;
