<template>
  <el-aside ref="sidenav" class="sidenav">
    <el-menu
      :collapse="isCollapsed"
      default-active="2"
      class="sidenav-menu"
      background-color="#545c64"
      text-color="#fff"
      @open="open"
      @close="close">
      <el-submenu :index="menuIndex+''" :key="menuIndex" v-for="(menu, menuIndex) in menus">
        <template slot="title">
          <i :class="menu.icon"></i>
          <span>{{menu.text}}</span>
        </template>

        <el-menu-item
          :class="{'active': item.routeName === $route.name}"
          :index="menuIndex+'-'+index"
          :key="index"
          v-for="(item, index) in menu.items"
          @click="goto(item)">
            {{item.text}}
        </el-menu-item>

      </el-submenu>

    </el-menu>
  </el-aside>
</template>

<script>
export default {
  data() {
    return {
      isCollapsed: false,
      menus: [
        {text: '文章', icon: 'el-icon-document', items: [
          {text: '全部文章', routeName: 'Articles'},
          {text: '新增文章', routeName: 'CreateArticle'},
          {text: '分類', routeName: 'ArticleCategory'},
          {text: '我的最愛', routeName: 'ArticleFavorite'},
          {text: '標籤', routeName: 'ArticleTag'}
        ]},
        {text: '追蹤碼', icon: 'el-icon-view', items: [
          {text: '新增追蹤碼', routeName: 'CreateTrackingCode'},
          {text: '插入追蹤碼', routeName: 'InsertTrackingCode'}
        ]},
        {text: '新聞', icon: 'el-icon-collection', items: [
          {text: 'PV', routeName: 'PageViews'},
          {text: '快速找記者', routeName: 'Reporter'}
        ]},
        {text: '媒體', icon: 'el-icon-video-camera', items: [
          {text: '媒體庫', routeName: 'Resources'},
          {text: '新增檔案', routeName: 'UploadResource'}
        ]},
        {text: '帳號', icon: 'el-icon-user', items: [
          {text: '全部帳號', routeName: 'Accounts'},
          {text: '新增帳號', routeName: 'CreateAccount'},
          {text: '權限管理', routeName: 'Authority'},
          {text: '角色管理', routeName: 'Roles'}
        ]},
        {text: '頁面', icon: 'el-icon-s-grid', items: [
          {text: '全部頁面', routeName: 'Pages'},
          {text: '鎖定首頁', routeName: 'LockPage'},
          {text: '鎖定頻道頁', routeName: 'LockChannel'}
        ]}
      ]
    };
  },

  computed: {
    windowWidth() {
      return this.$store.state.windowWidth;
    }
  },

  watch: {
    windowWidth() {
      if (this.windowWidth <= 574) {
        clearTimeout(this._timeout);
        this._timeout = setTimeout(() => this.toggle(), 100);
      }
    }
  },

  mounted() {
    // this.$nextTick(() => document.querySelector('aside > ul > li > div').click());
    document.querySelector('aside > ul > li > div').click();
  },

  methods: {
    goto({routeName}) {
      if (routeName !== this.$route.name) {
        this.$router.push({name: routeName});
      }
      if (this.windowWidth <= 574) {
        this.toggle();
      }
    },

    toggle() {
      if (this.windowWidth > 574) {
        this.$refs.sidenav.$el.classList.remove('sidenav--collapse');
        this.isCollapsed = !this.isCollapsed;
        return;
      }

      this.isCollapsed = false;
      this.$refs.sidenav.$el.classList.toggle('sidenav--collapse');
    },

    open(key, keyPath) {
      console.log(key, keyPath);
    },

    close(key, keyPath) {
      console.log(key, keyPath);
    }
  }
};
</script>

<style lang="scss">
@import "~@/style/variables.scss";

.sidenav {
  height: 100%;
  display: flex;
  flex-direction: column;
  width: auto !important;
  transition: transform 0.2s;
  z-index: 100;
}

.sidenav--collapse {
  transform: translate(-210px);
}

.sidenav-menu {
  flex-grow: 1;
  overflow-y: auto;
}

.sidenav, .sidenav-menu:not(.el-menu--collapse) {
  width: 210px;
}

.menu-item--active {
  color: $--color-identify
}

.el-menu-item {
  background-color: darken(rgb(84, 92, 100), 5%) !important;
}

.el-menu-item:hover {
  background-color: darken(rgb(84, 92, 100), 12%) !important;
}

.el-menu-item.is-active {
  color: white;
}

.active {
  color: $--color-identify !important;
}

@media screen and (max-width: 574px) {
  .sidenav {
    position: fixed;
    padding-top: 50px;
    height: 100%;
    top: 0;
    left: 0;
  }
}
</style>