<template>
  <div class="app">
    <router-view></router-view>
  </div>
</template>

<script>
export default {
  name: 'App',

  mounted() {
    console.log('resize');
    this.$store.commit('windowWidth', window.innerWidth);
    window.addEventListener('resize', this._onWindowResize.bind(this));
  },

  beforeDestroy() {
    window.removeEventListener('resize', this._onWindowResize);
  },

  methods: {
    _onWindowResize() {
      this.$store.commit('windowWidth', window.innerWidth);
    }
  }
};
</script>

<style lang="scss">
.app {
  width: 100%;
  height: 100%;
}
</style>
