<template>
  <el-header class="main-header" height="50px">
    <el-tooltip content="主選單" placement="bottom" >
      <button class="svg-btn svg-btn__menu btn-menu" @click="toggleMenu"></button>
    </el-tooltip>

    <div class="logo" @click="$router.push('/')">
      <img src="@/assets/logo1.png" alt="">
      <img v-if="!isSmallDevice" src="@/assets/logo2.png" style="margin-left: 0.5rem" alt="">
    </div>
    <!-- <span style="font-size: 1.2rem">編採後台</span> -->
    <span style="margin-left: auto; margin-right: 1rem">Yuska Wu</span>
    <el-tooltip content="登出" placement="bottom" >
      <el-button class="btn-signout" icon="el-icon-switch-button" size="mini" circle></el-button>
    </el-tooltip>
  </el-header>
</template>

<script>
export default {
  data() {
    return {};
  },

  computed: {
    windowWidth() {
      return this.$store.state.windowWidth;
    },

    isSmallDevice() {
      return this.windowWidth <= 574;
    }
  },

  methods: {
    toggleMenu() {
      this.$emit('toggleMenu');
    }
  }
};
</script>

<style lang="scss" scoped>
@import "~@/style/variables.scss";

.main-header {
  background-color: $--color-identify;
  display: flex;
  align-items: center;
  box-shadow: 0 1px 10px 1px rgba(0,0,0,0.2);
  color: rgba(0,0,0,0.8);
  z-index: 200;
}

.logo {
  height: 30px;
  margin: 0 2rem 0 2rem;
  flex-shrink: 0;
  cursor: pointer;
}

.logo img {
  max-width: 100%;
  max-height: 100%;
}

.btn-menu {
  margin: 0 1rem 0 1rem;
}

.btn-signout {
  margin: 0 1rem 0 1rem;
}
</style>